
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  /*margin-top: 60px;*/
}

@media only screen and (min-device-width: 640px) {
html {
    font-size: 78%;
}
}
@media only screen and (max-device-width: 640px) {
html {
    font-size: 2.666667vw;
}
}
body {
  margin: 0;
  height: 100vh;
  overflow: auto;
}
.page-content {
  position: relative;
  padding: 0 1.5rem;
}

.phone-cell-container {
  position: relative;
  margin: 2rem 0;
  width: 100%;
  /*border-bottom: 1px solid #D8D8D8;*/
  /*display: flex;*/
  /*align-items: center;*/
}
.phone-cell-container:hover {
  cursor: pointer;
}
.phone-cell-container .cell-icon {
  position: relative;
  display: inline-block;
  width: 31.2%;
  border-radius: 2px;
  padding-bottom: 23.4%;
  -webkit-box-shadow: 4px 2px 10px rgba(205, 205, 205, 0.3);
          box-shadow: 4px 2px 10px rgba(205, 205, 205, 0.3);
}
.phone-cell-container .cell-info {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 66%;
}
.phone-cell-container .cell-info .cell-title {
  font-size: 1.6rem;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  position: relative;
  word-wrap: break-word;
  word-break: break-all;
}
.phone-cell-container .cell-info .cell-date {
  position: absolute;
  bottom: 0.12rem;
  color: #99a2aa;
  width: 100%;
  font-size: 1.2rem;
}

body {
  margin: 0;
}
.cnbeta-article-container {
  word-break: break-all;
  font-family: "Lantinghei SC", "Open Sans", Arial, "Hiragino Sans GB", STHeiti, "WenQuanYi Micro Hei", SimSun, sans-serif;
  padding: 0 1.5rem;
  margin: auto;
  max-width: 1280px;
  color: #333;
}
@media only screen and (min-device-width: 640px) and (max-device-width: 1024px) {
html {
    font-size: 78%;
}
body {
    padding: 5px 70px;
}
}
@media screen and (max-device-width: 640px) {
html {
    font-size: 2.666667vw;
}
}
h1 {
  margin-top: 1rem;
  word-wrap: break-word;
  word-break: break-all;
  font-size: 2em;
  font-weight: bold;
  text-align: left;
  line-height: 1.8;
}
.news-info {
  color: #99a2aa;
  margin: 1.2rem 0 2rem 0;
  position: relative;
  font-size: 1.2rem;
}
.news-info.source {
  padding: 0 0.5rem;
}
.hairline {
  border-bottom: 1px solid #999999;
  -webkit-margin-before: 1em;
  -webkit-margin-after: 1em;
  -webkit-margin-start: auto;
  -webkit-margin-end: auto;
}
summary,
.content {
  font-size: 1.6rem;
  line-height: 1.8;
  text-align: justify;
  word-break: break-all;
}
summary {
  padding: 5px;
  background: #f0f0f0;
  /*border: 0.5px solid #aaaaaa;*/
}
.content > p {
  margin-bottom: 2rem;
}
img,
video,
object,
figure,
iframe {
  max-width: 100%;
  display: block;
  margin: auto;
}
iframe {
  border: none;
}
a:link {
  color: #3264c8;
}
strong,
b {
  font-weight: 500;
}
p {
  display: block;
  -webkit-margin-before: 1em;
  -webkit-margin-after: 1em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}
div.videoPlaceholder {
  margin: auto;
  width: 480px;
  height: 240px;
  background-color: black;
  position: relative;
}

.refresh-btn {
  position: fixed;
  bottom: 5%;
  right: 5%;
  width: 30px;
  height: 30px;
  border: 1px solid #ffffff;
  border-radius: 25px;
  background: #ffffff;
  -webkit-box-shadow: 0px 0px 10px #cccccc;
          box-shadow: 0px 0px 10px #cccccc;
  cursor: pointer;
}
.refresh-btn .refresh-icon {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  /*-webkit-transform: translate(-50%, -50%);*/
  /*transform: translate(-50%, -50%);*/
  margin-left: -10px;
  margin-top: -10px;
  background: url(../../static/img/clockwise-arrow.0e679c7.png) no-repeat;
  background-size: 100% 100%;
}
.refresh-btn .refresh-icon.loading-icon {
  -webkit-animation: rotate 1s linear 0s infinite;
  animation: rotate 1s linear 0s infinite;
}
@-webkit-keyframes rotate {
from {
    -webkit-transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
}
}
@keyframes rotate {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
